@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
  text-align: left;
  padding: 3% 6%; /* Increased padding */
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

.event-box {
  position: relative;
  min-height: 150px;
  border: #bcdcdb 1px solid;
  border-radius: 4px;
  margin-bottom: 35px;
  padding: 50px;
  background-color: #fff;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.event-box h3 {
  margin-top: 0; /* Adjust as needed */
}

.event-box p {
  margin: 5px 0; /* Space between paragraphs */
}

.event-box button {
  margin: 10px 0;
  line-height: 1.6;
  position: absolute;
  top: 13px; /* Adjust this value as needed for top spacing */
  right: 13px;
  padding: 10px 16px;
  border-radius: 4px;
  background-color: #074745;
  color: white;
  cursor: pointer;
  float: right;
  transition: background-color 0.3s ease; /* Added transition for smooth effect */
}

.event-box button:hover {
  background-color: #066666; /* A more subtle color change */
}

h1 {
  text-align: center;
  padding: 10px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 2.8rem;
  margin-bottom: 20px;
}

p {
  margin: 12px 0;
  font-size: 16px;
  line-height: 1.6;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
}

#city-search,
#number-of-events {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

#event-list {
  padding: 0;
  margin: 0 auto;
  max-width: 800px; /* Adjust width as needed */
  list-style-type: none;
}
